* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .App {
    text-align: center;
    font-family: Helvetica, sans-serif;
    background-color: rgb(34, 34, 34);
    height: 100vh;
    color: rgb(250, 250, 250);
  }
  .App-title {
    font-size: 50px;
    padding: 25px 0;
  }
  
  .Timers {
    display: flex;
    justify-content: center;
  }
  @media (max-width: 900px) {
    .Timers {
      flex-direction: column;
      align-items: center;
    }
  }
  @media (max-width: 900px) {
    .Stopwatch {
        margin-bottom: 40px;
    }
  }
  .Countdown,
  .Stopwatch {
    margin-left: 30px;
    margin-right: 30px;
    border: 2px solid grey;
    position: absolute;
    border-radius: 4px;
    padding: 20px;
    width: 400px;
    height: 260px;
    background-color: rgb(22, 27, 31);
    box-shadow: 0 3px 6px rgb(12, 12, 12);
  }
  .Countdown {
    padding-top: 10px;
  }
  
  .Countdown-header,
  .Stopwatch-header {
    font-size: 40px;
    margin-top: 10px;
    font-weight: bold;
  }
  
  button {
    background-color: #202b33;
    border: solid 1px transparent;
    border-radius: 4px;
    padding: 10px 20px;
    color: #ffffff;
    font-size: 16px;
    margin: 0 5px;
    cursor: pointer;
  }
  button:hover {
    background-color: #106ba3;
  }
  .Stopwatch button {
    padding: 12px 32px;
    font-size: 20px;
  }
  .Stopwatch-display {
    padding: 40px 0;
    font-size: 48px;
  }
  .Countdown-display {
    margin-top: 5px;
    margin-bottom: 20px;
  }
  .Countdown-display button {
    margin: 0 15px;
    border: solid 1px transparent;
    border-radius: 2px;
    padding: 4px 16px;
    color: #ffffff;
  
    font-size: 16px;
  }
  .Countdown-display button:hover {
    background-color: #106ba3;
  }
  .Countdown-label {
    font-size: 18px;
    margin-top: 5px;
    margin-bottom: 10px;
  }
  .Countdown-time {
    font-size: 36px;
    margin: 5px 0;
  }

  .dot {
    height: 30px;
    width: 30px;
    
    position: absolute;
    top: 593px;
    left: 185px;
    background-color: lightblue;
    border-radius: 100px;
  }

  .circle {
    background-color: rgb(34, 34, 34);
    border-color: lightblue;
    position: absolute;
    border-width: 1px;
    border-style: solid;
    width: 40px;
    height: 40px;
    top: 588px;
    left: 180px;
    border-radius: 100px;
    animation: animateBreath 3s ease-in-out infinite alternate;
  }
  
  #container {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }

  @keyframes animateBreath { 
    from {width: 40px; height: 40px; border-radius: 100px;top: 588px; left: 180px;}
    to {width: 600px; height: 600px; border-radius: 1500px; top: 308px; left: -95px;}
  }